.checkbox {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: center;
}
.checkbox > div {
  margin: 37px 0;
}
.checkbox > div > label {
  margin-left: 10px;
}
.d-none {
  display: none;
}
@media screen and (max-width: 320px) {
  .checkbox > div > label {
    margin-left: 0;
  }

  .inputs__container {
    margin-left: 0;
  }
  .checkbox button {
    margin-right: 10px;
    padding: 5px 20px !important;
  }
}
