.main {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("../../Images/7460148.jpg");
  background-size: cover;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
}
.main__container {
  background-color: #fff;
  border-radius: 15px;
  text-align: center;
  padding: 20px;
}
.main__container h1 {
  font-family: "Caveat", cursive;
  font-weight: bold;
  font-size: 50px;
  padding: 15px;
  background-color: rgb(252, 206, 40);
  margin: 30px 0;
  border-radius: 20px;
}
.main__container h2 {
  font-family: "Caveat", cursive;
  font-weight: 500;
  background-color: rgb(191, 26, 35);
  padding: 15px 30px;
  border-radius: 20px;
  color: #fff;
  font-size: 35px;
}

/* Css button */
.main__container button {
  --c: rgb(191, 26, 35); /* the color*/

  box-shadow: 0 0 0 0.1em inset var(--c);
  --_g: linear-gradient(var(--c) 0 0) no-repeat;
  background: var(--_g) calc(var(--_p, 0%) - 100%) 0%,
    var(--_g) calc(200% - var(--_p, 0%)) 0%,
    var(--_g) calc(var(--_p, 0%) - 100%) 100%,
    var(--_g) calc(200% - var(--_p, 0%)) 100%;
  background-size: 50.5% calc(var(--_p, 0%) / 2 + 0.5%);
  outline-offset: 0.1em;
  transition: background-size 0.4s, background-position 0s 0.4s;
}
.main__container button:hover {
  --_p: 100%;
  transition: background-position 0.4s, background-size 0s;
  color: #fff;
}
.main__container button:active {
  box-shadow: 0 0 9e9Q inset #0009;
  background-color: var(--c);
  color: #fff;
}

.main__container button {
  font-size: 3.5rem;
  cursor: pointer;
  font-weight: bold;
  border: none;
  border-radius: 10px;
  padding: 10px 15px;
  font-family: "Caveat", cursive;
}
