.welcome {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
  background-image: url("../../../Images/4070822.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left;
}
.welcome .welcome__container {
  background-color: #fff;
  padding: 50px;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url("../../../Images/wave\ \(1\).svg");
  background-repeat: no-repeat;
}
.welcome .welcome__container h1 {
  font-family: "Arvo", serif;
  font-size: 25px;
  margin-bottom: 30px;
}
.welcome .welcome__container input {
  padding: 20px;
  border-radius: 10px;
  border-color: #f9e601;
  font-size: 20px;
  font-weight: bold;
  font-family: "Arvo", serif;
}

.welcome .welcome__container button {
  display: inline-block;
  position: relative;
  padding: 20px;
  border-radius: 1rem;
  color: black;
  background-color: #f9e601;
  border-width: 2px 1px;
  border-style: solid;
  border-color: #f9e601;
  border-image: initial;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  outline: none;
  transition: all 200ms ease 0s;
  min-width: 0px;
  touch-action: manipulation;
  margin-left: 15px;
  text-decoration: none;
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 700;
  font-size: 25px;
  font-family: "Arvo", serif;
}
.welcome .welcome__container button:hover {
  background-color: #f9e001;
}
@media screen and (max-width: 400px) {
  .welcome__container {
    padding: 0 !important;
  }
  .welcome__container form input {
    margin-bottom: 15px;
  }
  .welcome__container form button {
    margin-bottom: 15px;
  }
}
