.game {
  font-family: "Arvo", serif;
  display: flex;
  justify-content: center;
  background-image: url("../../../Images/vecteezy_back-to-school-doodles-in-chalkboard-background_12086236.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
  font-size: 20px;
  background-position: center;
  height: 100vh;
}
.game .game__container {
  width: 80%;
}

.game .game__container .game__start button {
  padding: 10px 20px;
  border-style: none;
  background: yellow;
  font-size: 20px;
  border-radius: 10px;
  cursor: pointer;
  text-transform: capitalize;
  font-family: "Arvo", serif;
}
.inputs__container {
  width: 70%;
  margin-left: 25px;
}
.inputs__container > div {
  display: flex;
}
.game .game__container .game__inputs {
  display: flex;
}

/* input */
@import url("https://fonts.googleapis.com/css?family=Dosis");

:root {
  /* generic */
  --gutterSm: 0.4rem;
  --gutterMd: 0.8rem;
  --gutterLg: 1.6rem;
  --gutterXl: 2.4rem;
  --gutterXx: 7.2rem;
  --colorPrimary400: #7e57c2;
  --colorPrimary600: #5e35b1;
  --colorPrimary800: #4527a0;
  --fontFamily: "Dosis", sans-serif;
  --fontSizeSm: 1.2rem;
  --fontSizeMd: 1.6rem;
  --fontSizeLg: 2.1rem;
  --fontSizeXl: 2.8rem;
  --fontSizeXx: 3.6rem;
  --lineHeightSm: 1.1;
  --lineHeightMd: 1.8;
  --transitionDuration: 300ms;
  --transitionTF: cubic-bezier(0.645, 0.045, 0.355, 1);

  /* floated labels */
  --inputPaddingV: var(--gutterMd);
  --inputPaddingH: var(--gutterLg);
  --inputFontSize: var(--fontSizeLg);
  --inputLineHeight: var(--lineHeightMd);
  --labelScaleFactor: 0.8;
  --labelDefaultPosY: 50%;
  --labelTransformedPosY: calc(
    (var(--labelDefaultPosY)) - (var(--inputPaddingV) * var(--labelScaleFactor)) -
      (var(--inputFontSize) * var(--inputLineHeight))
  );
  --inputTransitionDuration: var(--transitionDuration);
  --inputTransitionTF: var(--transitionTF);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 10px;
}
.Input {
  margin-bottom: 20px;
  width: 100%;
}
.Input > span {
  font-size: 20px;
  color: red;
}
.Input-text {
  display: block;
  margin: 0;
  padding: var(--inputPaddingV) var(--inputPaddingH);
  width: 100%;
  font-family: inherit;
  font-size: var(--inputFontSize);
  font-weight: inherit;
  line-height: var(--inputLineHeight);
  border: none;
  border-radius: 0.4rem;
  transition: box-shadow var(--transitionDuration);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.Input-text:focus {
  outline: none;
  box-shadow: 1rem 1rem 0.2rem #f9e601;
}

/* labels */
.game__labels {
  display: flex;
  text-align: center;
}
.game__labels label {
  display: flex;
  flex-direction: column;
  margin: 0 5px;
}
.game__labels input {
  margin: 0;
}
.game__finish {
  background-color: red !important;
  color: #fff;
  width: 200px;
  padding: 15px 10px !important;
  transition: 0.3s ease-in-out !important;
}
.game__finish:hover {
  background-color: black !important;
}

.game .game__container p {
  color: black;
  background: #f9e601;
  display: inline-block;
  padding: 10px;
  margin: 25px 0 15px 0;
  border-radius: 10px;
  min-width: 100px;
  text-align: center;
}

.game__wheelButton {
  background-color: #30794b !important;
  color: #fff;
  transition: 0.3s ease-in-out;
}
.game__wheelButton:hover {
  background-color: #22693c !important;
}
